import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button as MaterialButton,
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { Fragment, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { MdDelete, MdEdit } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";

import { deleteUser, getAllUsers ,clearErrors} from "../../../../redux/actions/userAction";
import { DELETE_USER_RESET } from "../../../../redux/constants/userConstant";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import Loader from "../../../layout/Loader/Loader";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import "./Users.css";

const Users = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();

  const { isAuthenticated, user } = useSelector((state) => state.user);
  const { error, users } = useSelector((state) => state.getAllUsers);
  const {
    error: userDeleteError,
    isDeleted,
    loading,
  } = useSelector((state) => state.deleteUser);

  const [open, setOpen] = useState(false);

  const deleteUserToggle = () => {
    setOpen((old) => (old ? false : true));
  };

  const userDeleteHandler = (id) => {
    dispatch(deleteUser(id));
    setOpen(false);
  };

  const columns = [
    { field: "id", headerName: "User Id", flex: 0.5,minWidth: 180 },
    { field: "name", headerName: "Name", flex: 0.3 ,minWidth: 130},
    { field: "email", headerName: "Email", flex: 0.3,minWidth: 130 },
    { 
        field: "role",
         headerName: "Role",
          flex: 0.2,
          minWidth: 130,
          cellClassName: (params) => {
            return params.getValue(params.id, "role") === "admin"
              ? "greenColor"
              : "redColor";
          },
         },
    {
      field: "actions",
      headerName: "Actions",
      flex: 0.2,
      minWidth: 130,
      type: "number",
      sortable: false,
      renderCell: (params) => {
        return (
          <Fragment>
            <Link to={`/admin/user/${params.getValue(params.id, "id")}`}>
              <MdEdit />
            </Link>

            <Button onClick={deleteUserToggle}>
              <MdDelete />
            </Button>
            <Dialog
              aria-labelledby="simple-dialog-title"
              open={open}
              onClose={deleteUserToggle}
            >
              <DialogTitle>Delete Product</DialogTitle>
              <DialogContent className="submitDailog">
                Do you want to Delete this User{" "}
                {params.getValue(params.id, "name")}
              </DialogContent>

              <DialogActions>
                <MaterialButton onClick={deleteUserToggle} color="primary">
                  Cancel
                </MaterialButton>
                <MaterialButton
                  onClick={() =>
                    userDeleteHandler(params.getValue(params.id, "id"))
                  }
                  color="secondary"
                >
                  Delete
                </MaterialButton>
              </DialogActions>
            </Dialog>
          </Fragment>
        );
      },
    },
  ];

  const rows = [];
  users &&
    users.forEach((user) => {
      rows.push({
        id: user._id,
        name: user.name,
        email: user.email,
        role: user.role,
      });
    });

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }
    if (userDeleteError) {
      alert.error(userDeleteError);
      dispatch(clearErrors());
    }

    if (isDeleted) {
      alert.success("User Deleted Successfully");
      dispatch({ type: DELETE_USER_RESET });
    }

    dispatch(getAllUsers());
  }, [
    isAuthenticated,
    navigate,
    alert,
    user,
    dispatch,
    error,
    userDeleteError,
    isDeleted,
  ]);

  return (
    <Fragment>
      <MetaData title="All Products - Admin" />

      <div className="dashboard">
        <SideBar />

        <div className="productListContainer">
          <h1 id="productListHeading">All Users</h1>

          {loading ? (
            <Loader />
          ) : (
            <DataGrid
              columns={columns}
              rows={rows}
              pageSize={10}
              disableSelectionOnClick
              className="productListTable"
              autoHeight
            />
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default Users;
